@font-face {
  font-weight: 400;
  font-family: "Raleway";
  font-style: normal;
  font-display: swap;
  src:
    local("Raleway"),
    local("Raleway"),
    url("../fonts/Raleway-Regular.woff2") format("woff2"),
    url("../fonts/Raleway-Regular.woff") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "Raleway";
  font-style: normal;
  font-display: swap;
  src:
    local("Raleway"),
    local("Raleway"),
    url("../fonts/Raleway-Bold.woff2") format("woff2"),
    url("../fonts/Raleway-Bold.woff") format("woff");
}
